<template>
  <div class="app-container">
    <!--form 组件-->
    <el-row :gutter="24">
      <!--用户数据-->
      <el-col :xs="17" :sm="18" :md="24" :lg="24" :xl="24">
        <!--工具栏-->
        <div class="head-container">
          <!-- 搜索 -->
          <el-card class="filter-container" shadow="never">
            <div class="seach">
              <div>
                <i class="el-icon-search"></i>
                <span>筛选搜索</span>
              </div>
              <div>
                <el-button type="primary" size="small" @click="getData">查询</el-button>
                <el-button size="small" @click="reset">重置</el-button>
              </div>
            </div>
            <div>
              <el-form :inline="true" size="small" label-width="140px">
                <el-form-item label="页面名称：">
                  <el-input v-model="searchForm.pageName" clearable placeholder="输入页面名称" style="width: 200px;" class="filter-item"/>
                </el-form-item>
              </el-form>
            </div>
          </el-card>
          <!-- 新增 -->
        </div>
        <!-- 数据列表 - head-->
        <el-card class="operate-container" style="margin-top:10px" shadow="never">
          <div class="flex-row-between" style="width:100%;">
            <div>
              <i class="el-icon-tickets"></i>
              <span>数据列表</span>
            </div>
            <div style="padding-right:20px;">
              <el-button
                @click="$router.push({path:'/shop/shopRenovation'})"
                class="filter-item"
                size="medium"
                type="primary"
                icon="el-icon-plus"
              >新建微页面</el-button>
            </div>
          </div>
        </el-card>
        <!--表格渲染-->
        <el-card style="margin-top:20px;" class="filter-container" shadow="never">
          <el-table ref="multipleTable" v-loading="loading" fixed :data="list" size="small" style="width: 100%;margin-top: 10px;" border @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column fixed prop="date" label="页面名称" show-overflow-tooltip>
              <template slot-scope="scope">{{ scope.row.vpageName }}</template>
            </el-table-column>
            <el-table-column label="页面导航" show-overflow-tooltip>
              <template slot-scope="scope">
                {{scope.row.isIndex == 0 ? scope.row.navigationName : '系统配置导航'}}
              </template>
            </el-table-column>
            <el-table-column prop="city" label="是否为首页模板" show-overflow-tooltip>
              <template slot-scope="scope">{{ scope.row.isIndex == 0 ? '否' : '是' }}</template>
            </el-table-column>
            <el-table-column prop="address" label="最新修改时间" show-overflow-tooltip>
              <template slot-scope="scope">{{ scope.row.updateTime }}</template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="300">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="editor(scope.row)">编辑</el-button>
                <el-button type="text" size="small" v-if="scope.row.isIndex == 0" @click="setting(scope.row)">设为首页</el-button>
                <el-button v-if="scope.row.isIndex == 1" @click="getCode" type="text" size="small">预览首页</el-button>
                <el-button v-if="scope.row.isIndex == 0" @click="getVcode(scope.row)" type="text" size="small">预览微页面</el-button>
                <el-button type="text" size="small" @click="copy(scope.row)">复制</el-button>
                <el-button type="text" size="small" v-if="scope.row.isIndex == 0" @click="changeNav(scope.row)">修改导航</el-button>
                <el-button class="dangerFont" v-if="scope.row.isIndex == 0" type="text" size="small" @click="deletes(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
        <!--分页组件-->
        <div class="flex-row-between" style="padding-top:20px;">
          <div class="flex-row-start">
            <div class="flex-row-start">
              <el-checkbox @change="selectAllSureList" v-model="selectAllSure">当前页全选</el-checkbox>
              <div class="checkBoxDiv">
                已选择 <span style="color:#409EFF">{{multipleSelection.length}}</span> 项
              </div>
            </div>
            <div class="flex-row-start" style="margin-left:20px">
              <el-button @click="deleteAll" type="primary">批量删除</el-button>
              <el-button @click="changeAll" type="primary">批量设置导航</el-button>
            </div>
          </div>
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchForm.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- 系统导航设置 -->
    <el-dialog title="" :visible.sync="navShow" width="30%" >
      <el-select v-model="changeNavForm.navigaId" placeholder="请选择">
        <el-option
          v-for="item in shopNavList"
          :data-type="typeof item.id"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
      <el-button @click="getSelect">刷新</el-button>
      <el-button type="primary" @click="newPage">添加导航</el-button>
      <div class="flex-row-center" style="margin-top:40px;">
        <el-button style="margin-right:40px" @click="navShow = false">返 回</el-button>
        <el-button type="primary" :loading="configLoading" @click="subConfig">确 认</el-button>
      </div>
    </el-dialog>
    <!-- 首页预览 -->
    <el-dialog title="首页预览" :visible.sync="indexShow" width="30%" >
      <div class="imgCode">
        <el-tabs tab-position="left">
          <el-tab-pane label="H5">
            <img style="width:100px;height:100px;" :src="form.shopIndexImgUrl" alt="">
          </el-tab-pane>
          <el-tab-pane :disabled="form.shopMiniStatus == 2" label="小程序">
            <img style="width:100px;height:100px;" :src="form.miniIndexImgUrl" alt="">
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="flex-row-center" style="margin-top:40px;">
        <el-button style="margin-right:40px" @click="indexShow = false">关 闭</el-button>
      </div>
    </el-dialog>
    <!-- 微页面预览 -->
    <el-dialog title="首页预览" :visible.sync="vShow" width="30%" >
      <div class="imgCode">
        <el-tabs tab-position="left">
          <el-tab-pane label="H5">
            <img style="width:100px;height:100px;" :src="vform.shopIndexImgUrl" alt="">
          </el-tab-pane>
          <el-tab-pane :disabled="vform.shopMiniStatus == 2" label="小程序">
            <img style="width:100px;height:100px;" :src="vform.miniIndexImgUrl" alt="">
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class="flex-row-center" style="margin-top:40px;">
        <el-button style="margin-right:40px" @click="vShow = false">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getShopQrCode , getShopVpageQrCode } from '@/api/system/mpCode'
import {
  shopVpagePage,
  deleteShopVpageById,
  indexShopVpage,
  copyShopVpage,
  editShopVpageNaviga
} from "@/api/shop/shopDecoration";
import { getShopNavigationInfoNoPageList } from '@/api/shop/navBar'
export default {
  data() {
    return {
      loading: false,
      list:[],                      // 数据列表
      listTotal:0,                  // 数据总数
      multipleSelection: [],
      searchForm:{
        pageNum: 1,
        pageSize: 5
      },      
      selectAllSure:false,          // 全选
      shopNavList:[],
      navShow:false,
      changeNavForm:{               // 修改店铺导航
        navigaId:''
      },
      configLoading:false,
      form:{},
      indexShow:false,
      vform:{},
      vShow:false,
    };
  },
  created() {
    this.searchForm.shopId = this.shop.shopId
    this.getData()
  },
  methods: {
    getCode(){
      this.indexShow = true;
      getShopQrCode(this.shop.shopId).then(res=>{
        console.log('获取店铺二维码',res)
        if(res.code == 200){
          this.form = res.data
        }else{
          this.$message.error(res.message);
        }
      })
    },
    getVcode(e){
      this.vShow = true;
      getShopVpageQrCode(this.shop.shopId,e.vpageId).then(res=>{
        console.log('获取微页面二维码',res)
        if(res.code == 200){
          this.vform = res.data
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 获取数据
    getData(){
      console.log('this.searchForm',this.searchForm)
      this.loading = true;
      shopVpagePage(this.searchForm).then(res => {
        console.log('获取列表',res)
        if (res.code == 200) {
          this.list = res.data.list;
          this.listTotal = res.data.total;
          this.loading = false;
        }
      });
    },
    // 获取导航
    getSelect(){
      getShopNavigationInfoNoPageList(this.shop.shopId).then(res=>{
        console.log('权限配置下拉框',res)
        this.shopNavList = res.data
        this.shopNavList.forEach(e => e.id = '' + e.id)
      })
    },
    // 显示导航弹框
    changeNav(e){
      this.getSelect()
      this.changeNavForm.vpageId = e.vpageId;
      this.changeNavForm.navigaId = ''+e.navigationId;
      this.navShow = true;
    },
    changeAll(){
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择数据后再试');
        return;
      }
      let arr = []
      this.multipleSelection.forEach(e=>arr.push(e.vpageId))
      arr = arr.join(',')
      this.getSelect()
      this.changeNavForm.vpageId = arr
      this.navShow = true;
    },
    // 更改导航
    subConfig(){
      console.log('this.changeNavForm',this.changeNavForm)
      if(!this.changeNavForm.navigaId){
        this.$message.error('选择导航后再试');
        return;
      }
      this.configLoading = true;
      editShopVpageNaviga(this.changeNavForm).then(res=>{
        console.log('修改导航',res)
        if (res.code == 200) {
          this.$message({
            type: "success",
            message: "修改成功!"
          });
          this.navShow = false;
          this.getData()
          setTimeout(()=>{
            this.configLoading = false
          },1000)
        }else{
          this.$message.error(res.message);
        }
      })
      
    },
    // 打开运费模板新页面
    newPage(){
      let details = this.$router.resolve({
        name: "新建导航",
      });
      window.open(details.href, "_blank")
    },
    reset(){
      this.searchForm.pageName = ''
      this.getData()
    },
    // 分页 - 切换页面
    handleCurrentChange(val) {
      this.searchForm.pageNum = val;
      this.getData();
    },
    // 分页 -
    handleSizeChange(val) {
      this.searchForm.pageSize = val;
      this.getData();
    },
    //编辑
    editor(e) {
      this.$router.push({
        path: `/shop/shopRenovation?vpageId=${e.vpageId}`
      });
    },
    // 批量删除
    deleteAll(){
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择数据后再试');
        return;
      }
      this.$confirm('确定删除该微页面？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let arr = []
        this.multipleSelection.forEach(e=>arr.push(e.vpageId))
        arr = arr.join(',')
        this.deleteFun(arr)
      }).catch(() => {});
    },
    // 删除
    deletes(e) {
      this.$confirm("确定删除该微页面？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.deleteFun(e.vpageId)
      }).catch(() => {});
    },
    // 删除方法
    deleteFun(ids){
      deleteShopVpageById(ids).then(res=>{
        console.log('删除操作fun',res)
        if (res.code == 200) {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 设为首页模板
    setting(e) {
      this.$confirm("确定设为首页模板？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        indexShopVpage(e.vpageId,this.shop.shopId).then(res=>{
          console.log('设为首页',res)
          if(res.code == 200){
            this.$message({
              type: "success",
              message: "设置成功!"
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 复制
    copy(e) {
      this.$confirm("确定复制该模板？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        copyShopVpage(e.vpageId).then(res=>{
          console.log('复制导航',res)
          if(res.code == 200){
            this.$message({
              type: "success",
              message: "复制成功!"
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
        
      }).catch(() => {});
    },
    // 底部固定 - 全选
    selectAllSureList(){
      if (this.selectAllSure) {
        this.selectAllSure = false;
        this.$refs.multipleTable.toggleAllSelection();
      } else {
        this.selectAllSure = true;
        this.$refs.multipleTable.clearSelection();
      }
    },
    // 表单选择
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.selectAllSure = (this.multipleSelection.length == this.list.length) && (this.list.length != 0)
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
};
</script>

<style scoped>
.imgCode /deep/ .el-tabs__nav-scroll{
  border-bottom:none;
}
.seach {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.pagination-container {
  display: inline-block;
  margin-top: 20px;
  width: 100%;
  height: 50px;
  /* line-height: 57px; */
  padding-top: 0;
  padding-right: 55px;
  margin-left: -23px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 40px;
}
.checkBoxDiv{
  font-size: 14px;
  margin-left: 40px;
  line-height: 19px;
  color: #606266;
}
</style>
