import request from '@/utils/request'
// 导航icon
export function getShopNavigationIconList () {
  return request({
    url: `/sys/business/shopNavigation/getShopNavigationIconList`,
    method: 'get',
  })
}
// 查询导航列表
export function getShopNavigationInfoList(data) {
  return request({
    url: `/sys/business/shopNavigation/getShopNavigationInfoList`,
    method: 'post',
    data: data
  })
}
// 查询导航名称是否存在
export function isShopNavigationExist (data) {
  return request({
    url: `/sys/business/shopNavigation/isShopNavigationExist?shopId=${data.shopId}&name=${data.name}`,
    method: 'get',
  })
}
