import request from '@/utils/request'
// 分页查询门店接口
export function selectShopRoomList(data) {
  return request({
    url: `/sys/shopRoom/selectShopRoomList`,
    method: 'post',
    data: data
  })
}
// 发布
export function addShopVpage(data) {
  return request({
    url: `/sys/business/shopVpage/addShopVpage`,
    method: 'post',
    data: data
  })
}
// 编辑发布
export function editShopVpage(data) {
  return request({
    url: `/sys/business/shopVpage/editShopVpage`,
    method: 'post',
    data: data
  })
}
//店铺组件回显
export function findShopVpageById(vpageId) {
  return request({
    url: `/sys/business/shopVpage/findShopVpageById?vpageId=${vpageId}`,
    method: 'get',
  })
}
// 分页查询微页面接口
export function shopVpagePage(data) {
  return request({
    url: `/sys/business/shopVpage/shopVpagePage`,
    method: 'post',
    data: data
  })
}
// 根据ID删除店铺微页面
export function deleteShopVpageById(vpageId) {
  return request({
    url: `/sys/business/shopVpage/deleteShopVpageById?vpageId=${vpageId}`,
    method: 'delete',
  })
}
//设置首页模板
export function indexShopVpage(vid,sid) {
  return request({
    url: `/sys/business/shopVpage/indexShopVpage?vpageId=${vid}&shopId=${sid}`,
    method: 'get',
  })
}
//设置首页模板
export function copyShopVpage(vid) {
  return request({
    url: `/sys/business/shopVpage/copyShopVpage?vpageId=${vid}`,
    method: 'get',
  })
}
// 分页查询微页面接口
export function editShopVpageNaviga(obj) {
  return request({
    url: `/sys/business/shopVpage/editShopVpageNaviga?vpageId=${obj.vpageId}&navigaId=${obj.navigaId}`,
    method: 'get',
  })
}